import LgTypeService from '@/services/trade/LgTypeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let LgTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultLgTypeTableFilter: {
      type: String,
      default: null
    },
    defaultLgTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      lgTypeTableData: [],
      lgTypeTableFilterTimer: null,
      lgTypeTableFilter: this.defaultLgTypeTableFilter,
      lgTypeTableOrderBy: this.defaultLgTypeTableOrderBy,
      lgTypeTableContainer: 'loading-container',
      lgTypeTableBusy: false,
      lgTypeTableTotalRows: 0,
      lgTypeTableCurrentPage: 1,
      lgTypeTablePerPage: 10
    }
  },
  computed: {
    lgTypeTableNoDataText: function () {
      return this.lgTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    lgTypeTableTotalPages: function () {
      return Math.ceil(this.lgTypeTableTotalRows / this.lgTypeTablePerPage)
    },
    lgTypeTablePagingInfo: function () {
      return this.pagingInfo(this.lgTypeTableCurrentPage, this.lgTypeTablePerPage, this.lgTypeTableTotalRows)
    }
  },
  methods: {
    loadLgTypeTableData (loadingContainerId) {
      let self = this
      this.lgTypeTableBusy = true
      this.lgTypeTableContainer = loadingContainerId || this.lgTypeTableContainer
      this.startLoading(this.lgTypeTableContainer)
      LgTypeService.lgTypes({
        pageIndex: this.lgTypeTableCurrentPage - 1,
        pageSize: this.lgTypeTablePerPage,
        filter: this.lgTypeTableFilter,
        orderByField: this.lgTypeTableOrderBy,
        tradeBehaviourEnumId: this.tradeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.lgTypeTableContainer)
        self.lgTypeTableBusy = false
        self.lgTypeTableTotalRows = response.data.totalRows
        self.lgTypeTableData = response.data.lgTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.lgTypeTableContainer)
        self.lgTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshLgTypeTable () {
      this.loadLgTypeTableData()
    },
    filterLgTypeTable (filter) {
      let self = this
      if (this.lgTypeTableFilter !== filter) {
        clearTimeout(this.lgTypeTableFilterTimer)
        this.lgTypeTableFilterTimer = setTimeout(() => {
          self.lgTypeTableFilter = filter
          self.loadLgTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.lgTypeTableFilterTimer)
      }
    },
    orderLgTypeTable (orderBy, active) {
      this.lgTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadLgTypeTableData()
    }
  },
  watch: {
    lgTypeTableCurrentPage: function () {
      this.refreshLgTypeTable()
    },
    lgTypeTablePerPage: function () {
      this.refreshLgTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshLgTypeTable()
    }
  }
}

export default LgTypeTableMixin
